.headerWrap{position:fixed;top:0;left:0;width:100%;color:#fff;transition:all 0.35s ease;z-index:9}.headerWrap.on{background:#fff;color:#000}.headerWrap .row{position:relative;display:flex;height:80px;justify-content:space-between;align-items:center}.headerWrap .row .logo,.headerWrap .row .nav,.headerWrap .row .mobileNav{align-self:center}.headerWrap .row .mobileNav{position:relative}.headerWrap .row .mobileNav button{padding:10px 20px;border-radius:50px;border:none;font-size:20px;display:flex;cursor:pointer}.headerWrap .row .mobileNav button .ham{margin-right:10px}.headerWrap .row .mobileNav button .ham,.headerWrap .row .mobileNav button .person{align-self:center}.headerWrap .row .mobileNav .mobileNavList{display:none;position:absolute;top:100%;right:0;margin-top:10px;background:#fff;padding:10px 0px;color:#000;width:150px;border-radius:10px}.headerWrap .row .mobileNav .mobileNavList.opener{display:block}.headerWrap .row .mobileNav .mobileNavList ul li{line-height:30px;padding:0 20px}.headerWrap .row .mobileNav .mobileNavList ul li:nth-child(3){border-top:1px solid #ddd;padding-top:10px;margin-top:10px}.headerWrap .row .nav{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.headerWrap .row .nav .depth1{display:flex}.headerWrap .row .nav .depth1 li{position:relative;padding:15px 0;cursor:pointer;margin:0 30px}.headerWrap .row .nav .depth2{display:none;position:absolute;top:100%;left:0;width:100px;background:#fff;color:#000}.headerWrap .row .nav .depth2.on{display:block}

